import fs from '../system/fs';
import users from '../system/users';
export default {
  handler: function handler(args, session) {
    var name = args._[0];
    var home = args.home,
        group = args.group,
        supplementalGroups = args.supplementalGroups;
    users.add(name, home, group, supplementalGroups.split(','));
    fs.mkdir(home, session, users.ids(name));
    fs.mkdir("".concat(home, "/Documents"), session, users.ids(name));
    fs.mkdir("".concat(home, "/Code"), session, users.ids(name));
    fs.write("".concat(home, "/.bash_history"), '', session, users.ids(name));
    fs.write("".concat(home, "/.bash_profile"), "source /etc/profile\n\n    export NVM_DIR=\"/".concat(home, "}/.nvm\"\n    [ -s \"$NVM_DIR/nvm.sh\" ] && . \"$NVM_DIR/nvm.sh\"  # This loads nvm\n\n    nvm use 10"), session, users.ids(name));
  },
  args: {
    alias: {
      'home': ['h'],
      'group': ['g'],
      'supplementalGroups': ['G']
    }
  }
};